import './App.css';
import './main.css';
import { AssetChooser } from "./features/assetChooser/AssetChooser";
import { PlotViewer } from "./features/plotViewer/PlotViewer";
import React from "react";
import { useSelector, useDispatch } from 'react-redux';
import { setVisible } from "./features/infoModal/infoSlice";

function App() {

    const dispatch = useDispatch()
    const info = useSelector(state => state.info.visible)

    let explanation = "These plots display the results of principal component analysis on the raw counts of the promoter capture Hi-C experiment" +
        "(or the Z-scores generated from those counts, if that option is selected). Each plot shows the specified components on the two axes, " +
        "and if a specific cell type group is selected, data from those cell types are highlighted. Also, gene-specific data can be viewed. The plots" +
        "include data from three replicates for each cell type."

    return (
        <>
            <div className="App" style={info ? {transition: "filter 0.3s ease-out", filter: "blur(10px) opacity(50%) brightness(50%)"} : {transition: "filter 0.3s ease-in"}}>
                <AssetChooser />
                <PlotViewer />
            </div>
            { info &&
                <div style={{borderRadius: "15px", position: "absolute", top: "25vh", left: "25vw",
                    width: "50%", backgroundColor: "gray", textAlign: "center"}}>
                    <h3 style={{color: "antiquewhite"}}>PCA Figure Info</h3>
                    <p style={{margin: "3%"}}>{explanation}</p>
                    <button onClick={() => dispatch(setVisible(false))} style={{marginBottom: "3%"}}>Close</button>
                </div>
            }
        </>
    );
}

export default App;