import { createSlice } from '@reduxjs/toolkit';

const initialState = {"gene": "IL7R", "assetType": "All Cell Types", "components": "PC1 and PC2", "zscores": false}

const assetSlice = createSlice({
    name: 'assets',
    initialState,
    reducers: {
        setZscores(state, action) { state.zscores = action.payload },
        setComponents(state, action) { state.components = action.payload },
        setAssetType(state, action) { state.assetType = action.payload },
        setGene(state, action) { state.gene = action.payload }
    }
})

export const { setZscores, setComponents, setAssetType, setGene } = assetSlice.actions

export default assetSlice.reducer